<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
        <div class="chip">{{employee.department}}</div>{{ employee.name }}
        <router-link v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}" class="secondary-content">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating pulse btn-large waves-effect waves-light red">
        <i class="fa fa-plus">

        </i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
      name: 'dashboard',
      data(){
        return {
          employees: []
        }
      },
      created() {
        db.collection('employees').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            console.log(doc.data());
              const data = {
                'id':doc.id,
                'name':doc.data().name,
                'department': doc.data().department,
                'employee_id': doc.data().employee_id
              };
              this.employees.push(data)
          })
        })
      }
    }
</script>



